/**
 * 待开票数据
 */
import {
	mapGetters
} from 'vuex'
import {
	invoiceList,
	submitInvoice
} from '@/api/bill'
export default {
	props: {
		i: Number, // 每个tab页的专属下标
		index: { // 当前tab的下标
			type: Number,
			default () {
				return 0
			}
		}

	},
	data() {
		return {
			mescroll: null, //mescroll实例对象
			downOption: {
				autoShowLoading: true,
				textInOffset: '下拉即可刷新...',
				textOutOffset: '松开即可刷新...',
				textLoading: '努力加载中...'
			},
			upOption: {
				auto: true,
				noMoreSize: 5,
				empty: {
					tip: '没有查询到数据',
					icon: '',
				},
				textNoMore: '数据已全部加载'
			},
			isInit: false, // 列表是否已经初始化
			params: {
				invoiceStatus: 'none'
			},
			dataList: [],
			checkDataList: [],
			checkAllStatus: false
		}
	},
	computed: {
		...mapGetters({
			userInfo: 'user/userInfo'
		})
	},
	watch: {
		// 监听下标的变化
		index(val) {
			if (this.i === val) {
				this.mescroll.triggerDownScroll();
			}
		}
	},
	methods: {
		mescrollInit(mescroll) {
			this.mescroll = mescroll;
		},
		upCallback(mescroll) {
			this.params.page = mescroll.num;
			this.params.limit = mescroll.size;
			invoiceList(this.params).then(res => {
				let {
					data,
					code
				} = res
				if (code === '200') {
					if (mescroll.num == 1) this.dataList = [];
					this.dataList = this.dataList.concat(data.content);
					mescroll.endBySize(data.content.length, data.count);
					this.$nextTick(() => {
						mescroll.endSuccess(data.content.length)
					})
				}
				if (code === 4400) {
					mescroll.endErr()
				}
			}).catch(error => {
				mescroll.endErr()
			})
		},
		downCallback(mescroll) {
			mescroll.resetUpScroll();
		},
		//单选
		checkboxChange({
			detail
		}) {
			let values = detail.value
			let items = this.dataList;
			for (var i = 0, lenI = items.length; i < lenI; ++i) {
				const item = items[i]
				if (values.includes(item.id)) {
					this.$set(item, 'checked', true)
				} else {
					this.$set(item, 'checked', false)
				}
			}
			if (values.length === items.length) {
				this.checkAllStatus = true
			} else {
				this.checkAllStatus = false
			}
			this.checkDataList = values
		},
		//全选操作
		checkboxAllChage({
			detail
		}) {
			if (detail.value.length > 0) {
				let items = this.dataList;
				for (var i = 0, lenI = items.length; i < lenI; ++i) {
					this.$set(items[i], 'checked', true)
					this.checkDataList.push(items[i].id)
				}
				this.checkAllStatus = true
			} else {
				let items = this.dataList;
				for (var i = 0, lenI = items.length; i < lenI; ++i) {
					this.$set(items[i], 'checked', false)
					this.checkDataList.splice(0, this.checkDataList.length)
				}
				this.checkAllStatus = false
			}
		},
		//提交开票
		openInvoice() {
			let {
				checkDataList
			} = this
			if (checkDataList.length > 0) {
				submitInvoice({
					ids: checkDataList.join(',')
				}).then(res => {
					let {
						code,
						message
					} = res;
					if (code === '200') {

						uni.showToast({
							icon: 'success',
							title: '开票申请成功！'
						});
						//触发下来刷新
						this.mescroll.triggerDownScroll();
						//重置数据
						this.checkAllStatus = false;
						this.checkDataList = []

					} else {
						uni.showToast({
							icon: 'none',
							title: '开票申请失败！'
						});
					}
				}).catch(erro => {
					console.log("oo")
				})
			}
		}
	}
}
